/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import * as S from "./styles";
import { optionArr } from "../Header/Nav";
import { color } from "../../styles/color";
import { postWriting, postImg } from "../../utils/api/Writing";
import { useHistory } from "react-router";
const mainCategory = [
  { value: 0, name: "" },
  { value: 0, name: "" },
  { value: 0, name: "" },
  { value: 0, name: "" },
  { value: 0, name: "" },
  { value: 0, name: "/" },
];
let fileFormData = new FormData();
const Writing = () => {
  const [fileList, setFileList] = useState<FileList | null>();

  const [imgConsole, setImgConsole] = useState("+ ");
  const addFileFunc = (e: React.ChangeEvent<HTMLInputElement>) => {
    const nowFile = e.target.files;
    if (!nowFile) return;
    if (nowFile.length > 5) {
      alert(" 5   .");
      return;
    }
    setFileList(nowFile);
    for (let i = 0; i < nowFile.length; i++) {
      fileFormData.append("file", nowFile[i]);
    }
    setImgConsole("");
  };
  const renderImg = (): JSX.Element[] => {
    if (!fileList) return [];
    const list: JSX.Element[] = [];
    for (let i = 0; i < fileList.length; i += 1) {
      list.push(
        <S.Img key={i} src={URL.createObjectURL(fileList[i])} alt="" />
      );
    }
    return list;
  };
  const [aim, setAim] = useState(0);
  const getCategory = (value: number) => {
    for (let i = 0; i < 6; i += 1) {
      mainCategory[i].value = 0;
      if (i === value) {
        mainCategory[i].value = 1;
        setAim(i);
        setSubaim(0);
      }
    }
  };
  let subCategory: number[] = [];
  useEffect(() => {
    subCategory = [];
    for (let i = 0; i < optionArr[aim].length; i++) {
      subCategory.push(0);
    }
  }, [aim]);
  const [subaim, setSubaim] = useState(0);
  const onClicksubCategory = (value: number) => {
    if (!subCategory) {
      return;
    }
    setSubaim(value);
  };
  //input
  const [input, setInput] = useState({
    nowbuy: 0,
    startauction: 0,
    title: "",
    description: "",
  });
  const { nowbuy, startauction, title, description } = input;

  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const nextInput = {
      ...input,
      [e.target.name]: e.target.value,
    };
    setInput(nextInput);
  };
  const onChangeTextarea = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const nextInput = {
      ...input,
      description: e.target.value,
    };
    setInput(nextInput);
  };
  const history = useHistory();
  //submit
  const onClickSubmit = async () => {
    if (nowbuy === 0 || title === "" || description === "") {
      alert("  ");
    } else if (!fileFormData) {
      alert(" .");
    } else {
      const getWritingResponse = await postWriting({
        auctionPrice: startauction,
        category: mainCategory[aim].name,
        content: description,
        immePrice: nowbuy,
        productSaleStatus: "ON_SALE",
        subCategory: optionArr[aim][subaim],
        title: title,
      });
      fileFormData.set("productId", getWritingResponse.data.productId);
      const getImg = postImg(fileFormData);
      console.log(getImg);
      alert(" .");
      history.push("/");
    }
  };
  return (
    <S.Container>
      <S.Title>  </S.Title>
      <S.ImgWrapper>
        {renderImg()}
        <S.AddImg htmlFor="getFile">{imgConsole}</S.AddImg>
        <input
          type="file"
          id="getFile"
          style={{ display: "none" }}
          onChange={addFileFunc}
          accept=".jpg, jpeg, .png"
          multiple
        />
      </S.ImgWrapper>
      <S.ItemInfo>
        <S.ItemPrice>
          <div>
              :
            <S.PriceInput
              name="nowbuy"
              value={nowbuy}
              onChange={onChange}
              type="number"
            />
            
          </div>
          <div>
              :
            <S.PriceInput
              name="startauction"
              value={startauction}
              onChange={onChange}
              type="number"
            />{" "}
            
          </div>
        </S.ItemPrice>
        <S.CategoryWrapper>
          <span></span>
          <S.CategoryItem>
            {mainCategory.map((category, i) => (
              <span
                key={i}
                onClick={() => getCategory(i)}
                style={{
                  color: i === aim ? color.green : color.gray,
                }}
              >
                {category.name}
              </span>
            ))}
          </S.CategoryItem>
        </S.CategoryWrapper>
        <S.CategoryWrapper>
          <span></span>
          <S.CategoryItem>
            {optionArr[aim].map((category, i) => (
              <span
                key={i}
                onClick={() => onClicksubCategory(i)}
                style={{
                  color: i === subaim ? color.green : color.gray,
                }}
              >
                {category}
              </span>
            ))}
          </S.CategoryItem>
        </S.CategoryWrapper>
        <S.TitleInput
          placeholder=" "
          name="title"
          value={title}
          onChange={onChange}
        />
        <S.TitleTextarea
          placeholder=" "
          name="description"
          value={description}
          onChange={onChangeTextarea}
        />
      </S.ItemInfo>
      <S.SubmitBtn onClick={onClickSubmit}>  </S.SubmitBtn>
    </S.Container>
  );
};

export default Writing;
