import React, { useEffect, useState } from "react";
import * as S from "./style";
import { HeaderLogo, Chatting, Person, Sell } from "../../assets";
import { Link, useHistory } from "react-router-dom";
import Nav from "./Nav";
import { getUserInfo } from "../../utils/api/Mypage/index";
const Header = (): JSX.Element => {
  const history = useHistory();
  const [userName, setUserName] = useState<string>("");
  const setUserInformationFunc = async () => {
    const userInfo = await getUserInfo();
    setUserName(userInfo?.data?.body?.user?.username);
  };
  useEffect(() => {
    setUserInformationFunc();
  }, []);
  const logoutBtn = (event: React.MouseEvent<HTMLElement>) => {
    localStorage.removeItem("access_token");
    localStorage.removeItem("expire_at");
    alert(" .");
    history.push("/login");
  };
  return (
    <S.Header>
      <S.Logo to="/">
        <img src={HeaderLogo} alt="" />
        <span></span>
      </S.Logo>
      <nav>
        <Link to="/mypage">
          <img src={Person} alt="" />
          <span></span>
        </Link>
        <Nav />
      </nav>
      <div>
        <S.Chatting to="/writing">
          <img src={Sell} alt="" />
          <span></span>
        </S.Chatting>
        <S.Chatting to="/chat">
          <img src={Chatting} alt="" />
          <span></span>
        </S.Chatting>
      </div>

      <S.UserInfo>
        <S.MyProfile>
          <S.ProfileCircle />
          <span>{userName}</span>
        </S.MyProfile>

        <S.LogoutBtn onClick={logoutBtn}></S.LogoutBtn>
      </S.UserInfo>
    </S.Header>
  );
};
//..
export default Header;
